@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@400;600&family=Playfair+Display&display=swap);
html{font-family:"Josefin Sans", sans-serif}h1{text-align:center}li{list-style:none}.app__form{padding-left:40px;padding-bottom:20px}.app__title{font-size:36px}.app__subtitle{text-transform:uppercase}.app__select-name{margin-left:40px}.app__btn{margin-left:40px;font-size:14px;height:36px;color:#f8f6ed;background-color:#086f1e;border-color:#0b9645;border-radius:4px;text-transform:uppercase}.app__error{margin-left:20px;color:#ad2b2b;font-weight:700;font-size:24px}

.todoList__item{width:90%;height:200px;border-radius:2;background-color:#f0f884;padding-left:80px}.title{padding-top:20px;text-transform:uppercase}

.completed{padding:5px;font-weight:600;color:darkgreen;font-size:24px}.not_completed{padding:5px;font-weight:600;font-size:24px;color:darkred}

